import React, { Component } from 'react';

function displayOptions(type){
    if(type === 'input'){
    return (
        <>
        <option value="USD">US Dollar</option>
        <option disabled value="EUR">Euro</option>
        <option disabled value="GBP">British Pound</option>
        <option disabled value="CAD">Canadian Dollar</option>
        </>
    )}
    if(type === 'output'){
    return (
        <>
        <option value="USD">US Dollar</option>
        <option value="EUR">Euro</option>
        <option value="GBP">British Pound</option>
        <option value="CAD">Canadian Dollar</option>
        </>
    )}
}
    

class SelectCurrency extends Component {

    constructor(props) {
        super();
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(e) {
        this.props.onCurrencyChange(e, this.props.name);
    }

    render() {
        return (
            <>
                <label>
                    <p>{this.props.label}</p>
                    <select onChange={this.handleChange} value={this.props.value}>
                        {displayOptions(this.props.type)}
                    </select>
                </label>
            </>
        );
    }
}

export default SelectCurrency;