import React, {Component} from 'react';
import { currencyfreaks_URL_Latest, currencyfreaks_API_key} from './config';                  // * variables used to form the query
// * Components
import Header from './layout_components/Header/Header';
import CurrencyInput from './converter_components/currency_input';
import SelectCurrency from './converter_components/select_currency';
// * Helpers
import { Helpers } from './App_Helpers'; 
// * Style
import style from './App.module.css';




class App extends Component {

  constructor(props){
    super();
    this.state = {
      amount: '',
      input_currency: "USD",    // * 'USD' is the default value, the free account only allows to set up 'USD' to source in the query.
      output_currency: "GBP",
    };
    this.handleAmountChange = this.handleAmountChange.bind(this);
    this.handleCurrencyChange = this.handleCurrencyChange.bind(this);
    }

    
    // * the query includes  (currencylayer_URL_LIVE, currencylayer_API_key) keys accoun from currencylayer.
    // * plus (this.state.output_currency, this.state.input_currency) from state.
    fetchRate(){       
      const query = `${currencyfreaks_URL_Latest}?apikey=${currencyfreaks_API_key}&symbols=${this.state.output_currency}`;
      fetch(query)
     .then(res => res.json())
     .then(result => this.setState({rate: result.rates[`${this.state.output_currency}`]}));
    }
    
    // * this method fetch the query with the default value when mounting
    //todo:  it might be beneficial only fetch if the user enter a valid amount in the amount input field.
    componentDidMount(){  
      this.fetchRate();
    }  

    // * this method checks if the select_currency input has change, and if it has, it fetch the query with new params.
    componentDidUpdate(prevProps, prevState) {   
      if (this.state.output_currency !== prevState.output_currency) {
        this.fetchRate();
      }
    }

    handleAmountChange(amount){
      this.setState({amount});
    }

    handleCurrencyChange(e, name){
      const currency = e.target.value;
      this.setState({[name]: currency});
      setTimeout(() => {
        this.fetchRate();
      }, 500);
    }

    render(){
      const rate = this.state.rate;
      const amount = this.state.amount;
      const outputAmount =  Helpers.convert(amount,rate);
      return(
        <div className={style.app}>
          <Header />
          <main>
            <div className={style.box_input}>
              <form>
                <SelectCurrency    // * Select input to choose FROM which currency to convert the amount of money 
                  name="input_currency"
                  value={this.state.input_currency}
                  onCurrencyChange={this.handleCurrencyChange}
                  type="input"
                  label="From"
                />     
                <CurrencyInput        // * Input to enter the amount of money the user tries to convert
                  value={this.state.amount}
                  onAmountChange={this.handleAmountChange}
                />
              </form>
            </div>

            <div className={style.to}></div>

            <div className={style.box_output}>
              <form>                 
                <SelectCurrency             // * Select input to chose TO which currency to convert the amount of money
                  name="output_currency"
                  value={this.state.output_currency}
                  onCurrencyChange={this.handleCurrencyChange}
                  type="output"
                  label="To"
                /> 
                <div className={`${style.amount} ${style.amount_output}`}>{outputAmount}</div>  
              </form>  
            </div>
          </main>
        </div>
      );
    }
    
 
}

export default App;
